@import "../Main.scss";


.contact{
    position: absolute;
    width: 100vw;
    height: 80vh;
    background-color: $backgroundColor;
}

.background{
    top: -150px;
}

.content{
    height: 500px;
}

.gradiant{
    width: 100%;
    height: 100%;
    background: linear-gradient( to bottom, black ,white 10%, white 90%, black);
    mix-blend-mode: multiply;
}

.mainHeader{
    position: absolute;
    top: 45%;
    left: 50%;
    font-family: "Amiri-Italic";
    color: white;
    font-size: 80pt;
    transform: translate(-50%, -50%);

}

.textWrapper{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    padding:10vh 10vw;
    height: 100vh;
    width: 80vw;
    background-color: $backgroundColor;
}

