@import "./../Main.scss";

a, a:hover, a:focus, a:active {
    text-decoration: none;
    color: inherit;
}

.navigationWrapper{
    position: absolute;
    width: 100vw;
    height: 10vh;
    top: 0;
    background-color: black;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navigation{
    position: absolute;
    width: 60vw;
    height: 10vh;
    top: 0;
    right: 0;
    background-color: black;
    color: white;
    display: flex;  
    align-items: center;
    justify-content: space-evenly;
    font-family: "Assistant-Bold";
}


.logo{
    position: absolute;
    width: 40vw;
    height: 10vh;
    top:0;
    left: 0;
    display: flex;  
    justify-content:center;
    align-items: center;
    font-size: 25pt;
    font-family: "Amiri-Italic";
    cursor: pointer;
    opacity: 0.5;


}

.button{
    cursor: pointer;
&:hover{
    opacity:0.5;
    transition:0.2s;
}
}
