@import "../Main.scss";

.home{
    position: absolute;
    width: 100vw;
    height: 80vh;
    background-color: $backgroundColor;
}

.background{
top: -150px;
}

.content{
    height: 500px;
   

}

.gradiant{
    width: 100%;
    height: 100%;
    background: linear-gradient( to bottom, black ,white 10%, white 90%, black);

    mix-blend-mode: multiply;
}

.mainHeader{
    position: absolute;
    top: 45%;
    left: 50%;
    color: white;
    font-size: 80pt;
    opacity: 0.8;
    transform: translate(-50%, -50%);
    font-family: "Amiri-Italic";
}

.secondHeader{
    position: absolute;
    top: 60%;
    left: 50%;
    color: white;
    font-size: 35pt;
    font-family: "Amiri-Italic";
    opacity: 0.8;
    transform: translate(-50%, -50%);

}

.textWrapper{
    position: absolute;
    display: flex;
    align-items: top;
    justify-content: space-between;
    padding:10vh 10vw;
    // height: 100vh;
    width: 80vw;
    background-color: $backgroundColor;
}
