.contactItem {
    position: relative;
    width:45vh;
    height: 15vh;
    color: white;
    font-family: "Amiri-Bold";
    font-size: 20pt;


}

.icon{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 40px;
    height: 40px;
    mask-position: center;
    mask-size: contain;
    mask-repeat: no-repeat;
    background-color: blue;

}
.text{
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
}